<template>
  <div>
    <v-container v-if="!robonomicsStatus" fluid fill-height class="ml-0 px-3">
      <v-layout
        justify-center
        align-center
      >
        <v-flex text-xs-center>
          <h1>Load robonomics</h1>
          <v-progress-circular indeterminate color="primary"></v-progress-circular>
        </v-flex>
      </v-layout>
    </v-container>

    <v-container v-if="robonomicsStatus" grid-list-md class="ml-0 px-3">
      <v-layout row wrap>
        <v-flex md12>
          <v-card>
            <v-card-text>
              <v-container grid-list-md class="ml-0 px-3">
                <v-layout row wrap>
                  <v-flex md12>
                    Lighthouse: <a :href="`https://etherscan.io/address/${lighthouse.address}`" target="blank">{{ lighthouse.name }}</a>
                  </v-flex>
                </v-layout>
              </v-container>
              <v-divider />

              <v-container grid-list-md class="ml-0 px-3">
                <v-layout row wrap>
                  <v-flex md12>
                    <div>
                      Cost: {{ price.valueStr }} |
                      Balance: <a :href="`https://etherscan.io/token/${balance.address}?a=${account}`" target="blank">{{ balance.valueStr }}</a> |
                      Approved: {{ approveTrade.valueStr }}
                    </div>
                  </v-flex>
                </v-layout>
              </v-container>

              <v-btn
                v-if="approveTrade.value < price.value"
                :loading="loadingApprove"
                :disabled="loadingApprove || balance.value < price.value"
                color="primary"
                @click.native="sendApproveTrade"
              >
                Approve
              </v-btn>

              <v-btn
                v-if="approveTrade.value >= price.value"
                :loading="loadingOrder"
                :disabled="loadingOrder || balance.value < price.value"
                color="primary"
                @click.native="order"
              >
                Order
              </v-btn>

            </v-card-text>
          </v-card>
        </v-flex>

        <v-flex md12>
          <v-card v-if="liability">
          <!-- <v-card> -->
            <v-card-title primary-title>
              <div>
                <h3 class="headline mb-0">Send a file</h3>
              </div>
            </v-card-title>
            <v-flex>
              <form id="form_post"  enctype="multipart/form-data" @submit.prevent="handleSubmit">
                <div>
                  <label><b>Batch number:</b></label>
                  <input type="text" name="batchNumber" value="1"/>
                </div>
                <div>
                  <label><b>Place of production:</b></label>
                  <input type="text" name="placeOfProduction" value="St.Petersburg"/>
                </div>
                <div>
                  <label><b>Owner of the technology:</b></label>
                  <input type="text" name="technologyOwner" value="Nanodoctor"/>
                </div>
                <div>
                  <label><b>Who is responsible for selection:</b></label>
                  <input type="text" name="responsibleForSelection" />
                </div>
                <div>
                  <label><b>Who is responsible for batch:</b></label>
                  <input type="text" name="responsibleForBatch"/>
                </div>
                <div>
                  <input type="file" name="myfile" />
                </div>
                <div>
                  <input type="submit" value="Upload" />
                </div>
              </form>
            </v-flex>
          </v-card>
        </v-flex>

        <v-flex md12>
          <v-card v-if="liability">
            <v-card-title primary-title>
              <div>
                <h3 class="headline mb-0">Result</h3>
              </div>
            </v-card-title>
            <v-card-text>
              <Liability :liability="liability" />
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>

      <v-layout row wrap>
        <v-flex md6>
          <v-card>
            <v-card-title primary-title>
              <div>
                <h3 class="headline mb-0">Demands</h3>
              </div>
            </v-card-title>
            <v-card-text>
              <p class="t-break" v-for="(item, i) in demands" :key="i">
                <b>account: </b>{{ item.account }}<br/>
                <b>model: </b>{{ item.model }}<br/>
                <b>objective: </b>{{ item.objective }}<br/>
                <b>token: </b>{{ item.token }}<br/>
                <b>cost: </b>{{ item.cost }}<br/>
                <b>deadline: </b>{{ item.deadline }}
              </p>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md6>
          <v-card>
            <v-card-title primary-title>
              <div>
                <h3 class="headline mb-0">Offers</h3>
              </div>
            </v-card-title>
            <v-card-text>
              <p class="t-break" v-for="(item, i) in offers" :key="i">
                <b>account: </b>{{ item.account }}<br/>
                <b>model: </b>{{ item.model }}<br/>
                <b>objective: </b>{{ item.objective }}<br/>
                <b>token: </b>{{ item.token }}<br/>
                <b>cost: </b>{{ item.cost }}<br/>
                <b>deadline: </b>{{ item.deadline }}
              </p>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import { Token } from 'robonomics-js'
import getRobonomics from '../utils/robonomics'
import { formatDecimals, watchTx } from '../utils/utils'
import Liability from './Liability'
import * as config from '../config'

let robonomics

export default {
  name: 'order',
  components: {
    Liability
  },
  data () {
    return {
      token: null,
      decimals: 9,
      symbol: 'XRT',
      robonomicsStatus: false,
      loadingApprove: false,
      loadingOrder: false,
      price: {
        value: config.PRICE,
        valueStr: `${formatDecimals(config.PRICE, 9)} XRT`
      },
      balance: {
        address: '',
        value: 0,
        valueStr: '0 XRT'
      },
      approveTrade: {
        value: 0,
        valueStr: '0 XRT'
      },
      model: config.MODEL,
      account: '',
      lighthouse: {
        name: '',
        address: ''
      },
      liability: null,
      demands: [],
      offers: []
    }
  },
  created () {
    robonomics = getRobonomics(config.LIGHTHOUSE)
    robonomics.ready().then(() => {
      let ready
      if (config.TOKEN) {
        this.token = new Token(robonomics.web3, config.TOKEN)
        ready = this.token.call('decimals')
          .then((decimals) => {
            this.decimals = decimals
            return this.token.call('symbol')
          })
          .then((symbol) => {
            this.symbol = symbol
            this.price.valueStr = `${formatDecimals(this.price.value, this.decimals)} ${this.symbol}`
            return this.fetchData()
          })
      } else {
        this.token = robonomics.xrt
        ready = this.fetchData()
      }
      ready.then(() => {
        this.robonomicsStatus = true
        this.balance.address = this.token.address
        this.lighthouse.name = robonomics.lighthouse.name
        this.lighthouse.address = robonomics.lighthouse.address
        this.account = robonomics.account

        robonomics.getDemand(this.model, (msg) => {
          this.demands = [{ ...msg }, ...this.demands.slice(0, 10)]
        })
        robonomics.getOffer(this.model, (msg) => {
          this.offers = [{ ...msg }, ...this.offers.slice(0, 10)]
        })
        robonomics.getResult((msg) => {
          console.log('result unverified', msg)
          if (this.liability !== null && msg.liability === this.liability.address) {
            this.setResult(msg.result, false)
          }
        })
      })
    })
  },
  methods: {
    fetchData () {
      return this.token.call('balanceOf', [robonomics.account])
        .then((balanceOf) => {
          this.balance.value = balanceOf
          this.balance.valueStr = `${formatDecimals(balanceOf, this.decimals)} ${this.symbol}`
          if (balanceOf > 0) {
            return this.token.call('allowance', [robonomics.account, robonomics.factory.address])
          }
          return false
        })
        .then((allowance) => {
          if (allowance) {
            this.approveTrade.value = allowance
            this.approveTrade.valueStr = `${formatDecimals(allowance, this.decimals)} ${this.symbol}`
          }
        })
    },
    sendApproveTrade () {
      this.loadingApprove = true
      return this.token.send('approve', [robonomics.factory.address, (this.price.value * 100)], { from: robonomics.account })
        .then((r) => watchTx(r))
        .then(() => {
          this.loadingApprove = false
          return this.fetchData()
        })
        .catch(() => {
          this.loadingApprove = false
        })
    },
    setResult (result, check = true) {
      this.liability = {
        ...this.liability,
        result,
        check
      }
    },
    newLiability (liability) {
      console.log('liability demand', liability.address)
      return liability.getInfo()
        .then((info) => {
          this.liability = {
            address: liability.address,
            worker: liability.worker,
            ...info
          }
          liability.watchResult((result) => {
            console.log('result', result)
            this.setResult(result, true)
          })
          return true
        })
        .catch((e) => {
          console.log(e)
          setTimeout(() => {
            this.newLiability(liability)
          }, 2000)
        })
    },
    order () {
      this.liability = null
      this.loadingOrder = true
      web3.eth.getBlock('latest', (e, r) => {
        const demand = {
          objective: config.OBJECTIVE,
          token: this.token.address,
          cost: this.price.value,
          lighthouse: robonomics.lighthouse.address,
          validator: '0x0000000000000000000000000000000000000000',
          validatorFee: 0,
          deadline: r.number + 1000
        }
        robonomics.postDemand(this.model, demand)
          .then((liability) => this.newLiability(liability))
          .then(() => {
            this.loadingOrder = false
          })
          .catch((e) => {
            this.loadingOrder = false
          })
      })
    },
    handleSubmit (event) {
      let formData = new FormData()

      const f = event.target
      const batch = f.querySelector('input[name="batchNumber"]').value
      const placeOfProduction = f.querySelector('input[name="placeOfProduction"]').value
      const technologyOwner = f.querySelector('input[name="technologyOwner"]').value
      const responsibleForSelection = f.querySelector('input[name="responsibleForSelection"]').value
      const responsibleForBatch = f.querySelector('input[name="responsibleForBatch"]').value
      const finput = f.querySelector('input[type="file"]')
      console.log(finput)

      const message = batch + ' ' + placeOfProduction + ' ' + technologyOwner + ' ' + responsibleForSelection + ' ' + responsibleForBatch
      web3.personal.sign(web3.fromUtf8(message), web3.eth.coinbase, (e, r) => {
        console.log(r)
        // this.signature = r
        formData.append('myfile', finput.files[0], finput.files[0].name)
        formData.append('batchNumber', batch)
        formData.append('placeOfProduction', placeOfProduction)
        formData.append('technologyOwner', technologyOwner)
        formData.append('responsibleForSelection', responsibleForSelection)
        formData.append('responsibleForBatch', responsibleForBatch)
        formData.append('signature', r)

        fetch('https://quality.nanodoctor.pro/', {
          method: 'POST',
          body: formData
        }).then(function (response) {
          location.href = response.url
        })
      })
    }
  }
}
</script>



